import React, { Component } from 'react';
import {Link} from 'react-router-dom'
import ContentWrapper from '../Layout/ContentWrapper';
import { Row, Col, Card, CardBody, CardFooter, Button, Dropdown, DropdownMenu, DropdownToggle, DropdownItem } from 'reactstrap';
import axios from 'axios';
import {url_partenaires} from '../../../urls'
import HeadingMenu from '../../Common/HeadingMenu';

const Apercu = (props) => {
    const partenaire = props.partenaire
    const key =  partenaire.key
    const nom = partenaire.nom
    const description = partenaire.description
    const logo = partenaire.logo
    const services = partenaire.services
    let nb_service = 0
    if(partenaire.services) {
        nb_service = services.length
    }
    console.log(nb_service)
    return (
        <Card className="card-default">
            <CardBody className="text-center">
                <img className="mb-2 img-fluid rounded-circle thumb64" src={logo} alt="Contact"/>
                <h4>{nom}</h4>
                <p>{description}</p>
            </CardBody>
            <CardFooter className="d-flex">
                <Row>
                    <Col col="4" className="br">
                        <em className="icon-present fa-fw text-primary mr-3"></em>
                        <strong>+{nb_service} </strong>
                    </Col>{/*
                    <Col col="4" className="br">
                        <em className="fas fa-ticket-alt fa-fw text-warning mr-3"></em>
                        <strong>+{nb_ticket} </strong>
                    </Col>
                    <Col xs="4">
                        <em className="fas fa-recycle fa-fw text-success mr-3"></em>
                        <strong>+{nb_bte} </strong>
                    </Col>*/}
                </Row>
                <Link to={`/show_partenaire/${partenaire}`}className="ml-auto">
                    <Button className="btn btn-xs" color="success">En savoir plus</Button>
                </Link>
            </CardFooter>
        </Card>
    )  
}


class ListePartenaire extends Component {
    constructor(props, context) {
        super(props, context);
        this.state = { 
            partenaires: [],
            path: [
                { 
                    label: 'Home',
                    value: '/dashboard_client',
                },
                { 
                    label: 'Partenaires',
                    value: '/list_partenaire',
                },
                { 
                    label: 'Liste',
                    value: '',
                }
            ],
            isLoading: true,
        };  
    } 

    componentDidMount() {
        axios.get(url_partenaires)
        .then((res) => {
            const partenaires = res.data.data
            console.log(partenaires)
            this.setState({
                partenaires,
                isLoading: false,
            })
            this.setState({
                isLoading: false,
            })
        })
        .catch((error) => {
            console.log("get partenaires failed!", error)
        })
    }

    toggle = () => {
        this.setState({
            dropdownOpen: !this.state.dropdownOpen
        });
    }

    render() {
        const partenaires = this.state.partenaires
        const isLoading = this.state.isLoading
        return (
            isLoading? (
            <ContentWrapper>
                <HeadingMenu path = {this.state.path}/>
               <Row>
                    {partenaires? (
                        partenaires.map((partenaire) => {
                            return(
                                <Col lg="4" sm="6">
                                    <Apercu partenaire = {partenaire}/>
                                </Col>
                            )
                        })
                    ):(
                        <span> Aucun partenaire n'est abonné à ce service pour l'instant </span>
                    )}
                </Row>
            </ContentWrapper>
            ):(
                <div> En cours de chargement... </div>
            )
        );
    }

}

export default ListePartenaire;
